function my_script(){
//
let tabs = document.querySelectorAll('.catalog__tab-item');
let catalog = document.querySelectorAll('.catalog__content');


let link_det = document.querySelectorAll('.catalog-item__content .catalog-item__link');
let link_back = document.querySelectorAll('.catalog-item__list .catalog-item__link');


tabs.forEach(function(el){ //  
    
        el.addEventListener('click',function(event){// el-     tabs
            let target = event.target;//   
            console.log(target);
            document.querySelector('.catalog__tab-item_active').classList.remove('catalog__tab-item_active');//  tab
            document.querySelector('.catalog__content_active').classList.remove('catalog__content_active');//  
            el.classList.add('catalog__tab-item_active');
                                for(let i=0; i<3; i++){
                                    if( target == tabs[i]){
                                         tabs[i].classList.add('catalog__tab-item_active');//      
                                        catalog[i].classList.add('catalog__content_active');//      
                                     break;
                                }
                               
                            }
                        
                   
        });
});

 //
[].forEach.call(link_det, function(el){//  ""
    el.onclick = function(e){
        let parent = el.parentElement;//      , .. catalog-item__content
            parent.classList.remove('catalog-item__content_active');//   
        let list = parent.nextElementSibling; //  ,  catalog-item__list
            list.classList.add('catalog-item__list_active');     
    }
});
[].forEach.call(link_back, function(el){//  ""
    el.onclick = function(e){
        let parent = el.parentElement;//      , .. catalog-item__list
            parent.classList.remove('catalog-item__list_active');//   
        let list = parent.previousElementSibling; //  ,  catalog-item__content
            list.classList.add('catalog-item__content_active');     
    }
});
// 
let button_large = document.querySelector('.button_large');
let button = document.querySelector('.header__tel .button');
let button_submit = document.querySelector('.button_submit');
let button_card = document.querySelectorAll('.button_card');
let close = document.querySelectorAll('.modal__close');

    button.onclick = function(e){
        document.querySelector('.overlay').style.display = "block";
        document.querySelector('.overlay .modal:nth-child(1)').style.display = "block";
    };
    button_large.onclick = function(e){
        document.querySelector('.overlay').style.display = "block";
        document.querySelector('.overlay .modal:nth-child(1)').style.display = "block";
    };
    button_submit.onclick = function(e){
        document.querySelector('.overlay').style.display = "block";
        document.querySelector('.overlay .modal:nth-child(1)').style.display = "block";
    };
    // ,    
    [].forEach.call(button_card, function(el){//  
        el.onclick = function(e){
        document.querySelector('.overlay').style.display = "block";    
        document.querySelector('.overlay .modal:nth-child(2)').style.display = "block";    
        //   (   )
        //el.parentElement.previousElementSibling.
        }
    });    
    [].forEach.call(close, function(el){//  
    el.onclick = function(e){
        el.parentElement.style.display = "none";
        el.parentElement.parentElement.style.display = "none";   
    }
});
}
module.exports = my_script;
 

